<template>
  <section class="about" id="about" ref="about">
    <div class="container">
        <div class="image">
            <img :src="about.image" :alt="about.title">
        </div>
        <div class="content">
            <h4 class="title">{{$t("About us")}}</h4>
            <p class="text">{{about.description}}</p>
        </div>
    </div>
  </section>
</template>

<script>
import { aboutData } from "@/api/Home/about.js";

export default {
  name: "about-home",
  data() {
    return {
      check: "",
      about: {},
    };
  },
  created() {
    aboutData().then((response) => {
      this.check = response.data.status;
      this.about = response.data.data;
      //   console.log(this.header);
      return response;
    });
  },
};
</script>