<template>
  <form v-on:submit.prevent="SubmitForm" class="profile_form">
    <div class="row">
        <div class="col-lg-6 col-md-12 col-12">
            <div class="form-group">
                <label for="">{{$t("Name")}}</label>
                <input type="text" name="" id="" class="form-control" v-model="Form.name" :placeholder="$t('Name')" required>
            </div>
        </div>
        <div class="col-lg-6 col-md-12 col-12">
            <div class="form-group">
                <label for="">{{$t("E-Mail")}}</label>
                <input type="text" name="" id="" class="form-control" v-model="Form.email" :placeholder="$t('E-Mail')" required>
            </div>
        </div>
        <div class="col-lg-6 col-md-12 col-12">
            <div class="form-group">
                <label for="">{{$t("Address")}}</label>
                <input type="text" name="" id="" class="form-control" v-model="Form.address" :placeholder="$t('Address')">
            </div>
        </div>
        <div class="col-lg-6 col-md-12 col-12">
            <div class="form-group">
                <label for="">{{$t("Phone")}}</label>
                <input type="text" name="" id="" class="form-control" v-model="Form.phone" :placeholder="$t('Phone')">
            </div>
        </div>
        <div class="col-12">
            <button type="submit" class="btn submit_btn">{{$t("save changes")}}</button>
        </div>
    </div>
  </form>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";

export default {
    data() {
        return {
            Form: {
                name: localStorage.getItem("name"),
                email: localStorage.getItem("email"),
                address: localStorage.getItem("address"),
                phone: localStorage.getItem("phone")
            }
        }
    },
    methods: {
        SubmitForm() {
        const newLocal = this.$i18n.locale;
        axios.defaults.headers.common["Accept-Language"] = newLocal;
        axios.defaults.headers.common["Content-Type"] = "multipart/form-data";
        axios
            .put("/profile/update_information", this.Form)
            .then((data) => {
            // console.log(data);
            if (data.data.status == true) {
                //Perform Success Action
                Swal.fire({
                    position: "top-end",
                    icon: "success",
                    title: data.data.message,
                    showConfirmButton: false,
                    timer: 1500,
                });
                localStorage.setItem('name', data.data.data.name);
                localStorage.setItem('email', data.data.data.email);
                localStorage.setItem('address', data.data.data.address);
                localStorage.setItem('phone', data.data.data.phone);
            } else {
                //Perform Success Action
                Swal.fire({
                    type: "error",
                    position: "top-end",
                    icon: "error",
                    title: data.data.message,
                    showConfirmButton: false,
                    timer: 1500,
                });
            }
            })
            .catch((error) => {
                Swal.fire({
                    type: "error",
                    position: "top-end",
                    icon: "error",
                    title: error.response.data.message,
                    showConfirmButton: false,
                    timer: 1500,
                });
            })
            .finally(() => {
            //Perform action in always
            });
        },
    },
}
</script>

