<template>
  <section class="booking">
    <div class="container">
      <div class="row">
        <div class="col-12">
          <table id="table">
            <thead>
              <tr>
                <th scope="col">{{ $t("booking_number") }}</th>
                <th scope="col">{{ $t("counters count") }}</th>
                <th scope="col">{{ $t("employee name") }}</th>
                <th scope="col">{{ $t("Date") }}</th>
                <th scope="col">{{ $t("status") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(booking, index) in bookings" :key="index">
                <td :data-label="$t('booking_number')" class="booking_number"><router-link :to="`/book/?track=${booking.Booking_number}`">{{ booking.Booking_number }}</router-link></td>
                <td :data-label="$t('counters count')">{{ booking.container_count }}</td>
                <td :data-label="$t('employee name')">{{ booking.employee }}</td>
                <td :data-label="$t('Date')">{{ booking.discharge_date }}</td>
                <td :data-label="$t('status')">{{ booking.status }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { bookingData } from "@/api/profile/booking.js";
import "jquery/dist/jquery.min.js";
import "bootstrap/dist/css/bootstrap.min.css";
import "datatables.net-dt/js/dataTables.dataTables";
import "datatables.net-dt/css/jquery.dataTables.min.css";
import $ from "jquery";

export default {
    name: "profile-page",
    data() {
      return {
        check: "",
        bookings: [],
      };
    },
    mounted() {
      bookingData().then((response) => {
        this.check = response.data.status;
        this.bookings = response.data.data;
        // console.log(this.employees);
        return response;
      });
    },
    updated() {
    $("#table").DataTable({
      retrieve: true,
      pageLength : 4,
      language: {
        search: this.$t("search"),
        lengthMenu: this.$t("show") + " _MENU_ " + this.$t("employees"),

        paginate: {
          first: this.$t("first"),
          last: this.$t("last"),
          next: this.$t("next"),
          previous: this.$t("prev"),
        },
        info:
          this.$t("show") +
          " _START_ " +
          this.$t("From") +
          " _END_ " +
          this.$t("to") +
          " _TOTAL_ " +
          this.$t("employees"),
      },
    });
  },
}
</script>

<style scoped>
    .booking_number{
        font-family: "bold" !important;
        text-decoration: underline;
        color: #000;
    }
    .booking_number a{
        font-family: "bold" !important;
        text-decoration: underline;
        color: #000;
    }
</style>